import React, { useContext } from 'react';
import {
  Box,
  Paper,
  Typography,
  Grid,
  Divider,
  CircularProgress
} from '@mui/material';
import GamificationContext from '../../context/GamificationContext';
import PointsBadge from './PointsBadge';
import LevelBadge from './LevelBadge';
import StreakCounter from './StreakCounter';
import DailyGoalProgress from './DailyGoalProgress';
import BadgesList from './BadgesList';

const GamificationSummary = () => {
  const { gamificationData, loading } = useContext(GamificationContext);
  
  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
        <CircularProgress />
      </Box>
    );
  }
  
  if (!gamificationData) {
    return (
      <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center', p: 2 }}>
        Gamification data not available.
      </Typography>
    );
  }
  
  return (
    <Paper sx={{ p: 3, mb: 4 }}>
      <Typography variant="h5" gutterBottom>
        Your Learning Progress
      </Typography>
      
      <Grid container spacing={3} sx={{ mb: 3 }}>
        <Grid item xs={12} sm={6} md={3}>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', p: 2 }}>
            <PointsBadge size="large" />
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              Total Points
            </Typography>
          </Box>
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', p: 2 }}>
            <LevelBadge showProgress={true} size="large" />
          </Box>
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', p: 2 }}>
            <StreakCounter size="large" />
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              Learning Streak
            </Typography>
          </Box>
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', p: 2 }}>
            <DailyGoalProgress size="large" />
          </Box>
        </Grid>
      </Grid>
      
      <Divider sx={{ my: 3 }} />
      
      <Box sx={{ mb: 2 }}>
        <Typography variant="h6" gutterBottom>
          Learning Stats
        </Typography>
        
        <Grid container spacing={2}>
          <Grid item xs={6} sm={3}>
            <Box sx={{ textAlign: 'center', p: 1 }}>
              <Typography variant="h4" color="primary.main">
                {gamificationData.stats.sessionsCompleted}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Sessions
              </Typography>
            </Box>
          </Grid>
          
          <Grid item xs={6} sm={3}>
            <Box sx={{ textAlign: 'center', p: 1 }}>
              <Typography variant="h4" color="secondary.main">
                {gamificationData.stats.questionsAsked}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Questions
              </Typography>
            </Box>
          </Grid>
          
          <Grid item xs={6} sm={3}>
            <Box sx={{ textAlign: 'center', p: 1 }}>
              <Typography variant="h4" color="success.main">
                {gamificationData.stats.contentCreated}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Content Created
              </Typography>
            </Box>
          </Grid>
          
          <Grid item xs={6} sm={3}>
            <Box sx={{ textAlign: 'center', p: 1 }}>
              <Typography variant="h4" color="warning.main">
                {Math.round(gamificationData.stats.totalLearningTime / 60)}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Hours Learning
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </Box>
      
      <Divider sx={{ my: 3 }} />
      
      <Box>
        <Typography variant="h6" gutterBottom>
          Recent Achievements
        </Typography>
        
        {gamificationData.badges.length > 0 ? (
          <BadgesList showUnearned={false} maxDisplay={4} />
        ) : (
          <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center', p: 2 }}>
            You haven't earned any badges yet. Keep learning to unlock achievements!
          </Typography>
        )}
      </Box>
    </Paper>
  );
};

export default GamificationSummary;
