import React, { useContext, useEffect } from 'react';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Avatar,
  Tooltip,
  CircularProgress,
  Chip
} from '@mui/material';
import GamificationContext from '../../context/GamificationContext';

const BadgesList = ({ showUnearned = true, maxDisplay = null }) => {
  const { 
    fetchBadges, 
    badges, 
    getUserBadges, 
    loading 
  } = useContext(GamificationContext);
  
  useEffect(() => {
    fetchBadges();
  }, [fetchBadges]);
  
  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
        <CircularProgress />
      </Box>
    );
  }
  
  if (!badges || badges.length === 0) {
    return (
      <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center', p: 2 }}>
        No badges available yet.
      </Typography>
    );
  }
  
  const userBadges = getUserBadges();
  const userBadgeIds = userBadges.map(badge => badge.badge._id || badge.badge);
  
  // Filter badges based on whether they're earned and the showUnearned prop
  const filteredBadges = badges.filter(badge => {
    const isEarned = userBadgeIds.includes(badge._id);
    return isEarned || showUnearned;
  });
  
  // Limit the number of badges displayed if maxDisplay is set
  const displayBadges = maxDisplay ? filteredBadges.slice(0, maxDisplay) : filteredBadges;
  
  // Get category color
  const getCategoryColor = (category) => {
    switch (category) {
      case 'learning':
        return 'primary';
      case 'engagement':
        return 'secondary';
      case 'achievement':
        return 'success';
      case 'special':
        return 'warning';
      default:
        return 'default';
    }
  };
  
  return (
    <Grid container spacing={2}>
      {displayBadges.map((badge) => {
        const isEarned = userBadgeIds.includes(badge._id);
        const earnedBadge = userBadges.find(b => (b.badge._id || b.badge) === badge._id);
        
        return (
          <Grid item xs={6} sm={4} md={3} key={badge._id}>
            <Card 
              sx={{ 
                height: '100%',
                opacity: isEarned ? 1 : 0.6,
                transition: 'all 0.3s ease',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: 3
                }
              }}
            >
              <CardContent sx={{ textAlign: 'center', p: 2 }}>
                <Avatar
                  src={badge.icon}
                  alt={badge.name}
                  sx={{
                    width: 64,
                    height: 64,
                    margin: '0 auto 12px',
                    filter: isEarned ? 'none' : 'grayscale(100%)',
                    bgcolor: isEarned ? 'primary.main' : 'grey.300'
                  }}
                >
                  {badge.name.charAt(0)}
                </Avatar>
                
                <Typography variant="subtitle1" component="div" gutterBottom noWrap>
                  {badge.name}
                </Typography>
                
                <Tooltip title={badge.description}>
                  <Typography 
                    variant="body2" 
                    color="text.secondary" 
                    sx={{ 
                      mb: 1,
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      display: '-webkit-box',
                      WebkitLineClamp: 2,
                      WebkitBoxOrient: 'vertical',
                      height: '40px'
                    }}
                  >
                    {badge.description}
                  </Typography>
                </Tooltip>
                
                <Box sx={{ display: 'flex', justifyContent: 'center', gap: 0.5, flexWrap: 'wrap' }}>
                  <Chip 
                    label={`Level ${badge.level}`} 
                    size="small" 
                    color={isEarned ? 'primary' : 'default'}
                  />
                  <Chip 
                    label={badge.category} 
                    size="small" 
                    color={isEarned ? getCategoryColor(badge.category) : 'default'}
                  />
                </Box>
                
                {isEarned && earnedBadge && (
                  <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mt: 1 }}>
                    Earned on {new Date(earnedBadge.earnedAt).toLocaleDateString()}
                  </Typography>
                )}
              </CardContent>
            </Card>
          </Grid>
        );
      })}
    </Grid>
  );
};

export default BadgesList;
